body {
  background-image: url(../assets/images/fondDOP.png);
  background-repeat: no-repeat;
  width: 100%;
  background-size: cover;
  background-position-x: center;
}

.logo {
  width: 320px;
}

p, .titre, footer, span {
  color: #f7f7f7;
  font-family: "Quicksand", sans-serif;
}

p {
  font-size: 18px;
}

.titre--deux {
  font-size: 36px;
}

.titre--un {
  font-size: 51px;
}

header {
  text-align: center;
  height: 100vh;
  margin-top: 15vh;
}

.fleche {
  width: 51px;
  margin-top: 144px;
}

.icone {
  height: 180px;
  margin: auto;
  display: flex;
  margin-bottom: 51px;
}

.contenue {
  margin: 0px 18px 0px 18px;
}

.contenue__ecout {
  margin-bottom: 102px;
  border-bottom: 2px solid #f7f7f7;
  border-radius: 1px;
}

.contenue__ecout p, .contenue__apprend {
  margin-bottom: 102px;
}

.navfo__liste--li a, .footer__info a {
  color: var(--hover-color);
  text-decoration: none;
  padding: 5px;
  border-radius: 10px;
}

.navfo__liste--li a:hover, .footer__info a:hover:not(.footer__info--nohover) {
  background-color: var(--hover-color);
}

.navfo__liste--li a:visited, .footer__info a:visited:not(.footer__info--nohover) {
  background-color: var(--hover-color);
}

.footer__info {
  padding: 9px 0 9px 0;
  text-align: center;
  font-size: 18px;
  font-weight: normal;
  text-decoration: none;
}

.footer__info p {
  width: 100%;
}

.footer__info--logodwm {
  height: 40px;
  width: 60px;
}

/* DESKTOPS DESKTOPS DESKTOPS DESKTOPS DESKTOPS DESKTOPS DESKTOPS DESKTOPS DESKTOPS DESKTOPS DESKTOPS DESKTOPS DESKTOPS */
@media only screen and (min-width: 1000px) {
  .logo {
    width: 320px;
  }

  p {
    font-size: 20px;
  }

  .titre--deux {
    font-size: 40px;
  }

  .titre--un {
    font-size: 57px;
  }

  header {
    text-align: center;
    margin: 0px auto 226px auto;
    justify-content: center;
    display: flex;
  }

  .icone {
    margin-bottom: 57px;
  }

  .contenue {
    margin: 0px 20px 0px 20px;
  }

  .contenue__ecout {
    margin-bottom: 113px;
  }

  .contenue__ecout p, .contenue__apprend {
    margin-bottom: 113px;
  }

  .footer__info {
    padding: 10px 0 10px 0;
    font-size: 20px;
  }
}
.navfo__liste {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 36px;
  list-style-type: none;
  padding-inline-start: 0px;
  margin-left: auto;
  margin-right: auto;
  column-gap: 18px;
  width: max-content;
}

.navfo__liste--credits {
  grid-column-start: 2;
  grid-row-start: 1;
  text-align: center;
}

.navfo__liste--ui {
  grid-column-start: 3;
  grid-row-start: 1;
  text-align: center;
}

.navfo__liste--case {
  grid-column-start: 1;
  grid-row-start: 1;
  text-align: center;
}

body[data-theme=dark] body {
  background-color: var(--fond-color);
}